<template>
  <div class="leavemessage">
    <i class="el-icon-back allback" @click="$emit('back')"></i>
    <el-button v-if="isLogin && !isDelete" class="deletebtn" type="info" plain  @click="isDelete = !isDelete">编 辑</el-button>
    <div class="messagebox" @click="onblur">
      <div class="msgitem" v-for="(item, index) in comment" :key="index">
        <div class="head">
          <el-avatar :src="item.head_img" icon="el-icon-user-solid"></el-avatar>
          <div class="name">{{item.name}}</div>
          <div class="time">{{item.time |time}}</div>
        </div>
        <div class="msg" v-html="item.comment"></div>
         <el-button @click="deleteMsg(item.id)" class="deletebtn item"  v-if="isDelete" type="danger" icon="el-icon-delete" circle></el-button>
      </div>
    </div>
    <div class="pinlunbox" :class="{'h':!shuruflage}">
      <p @click="shuruflage = !shuruflage" :class="{'pright':!shuruflage}">发表留言：</p>
      <editor @change="liuyanchange" />
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import editor from "../../components/editor";
export default {
  components: {
    editor
  },
  computed: {
    ...mapState([
      "allnavs", //导航数据
      "isLogin", //是否是登录状态
      "USER_ID" //用户ID
    ])
  },
  data() {
    return {
      shuruflage: false,
      comment: [],
      isDelete:false
    };
  },
  methods: {
    // 删除留言
    deleteMsg(id){
      this.$http.delete("/comment/"+id).then(({ data: res }) => {
        if (res.ok == 0)
          return this.$notify.error({
            title: "删除",
            customClass: "alter"
          });
          this.getmsg()
      });
    },
    // 获取留言
    async getmsg() {
      let { data: res } = await this.$http.get("/comment/" + this.USER_ID);
      console.log(res);
      this.comment = res.data;
    },
    onblur() {
      this.shuruflage = false;
      this.isDelete = false
    },
    liuyanchange(data) {
      data.user_id = this.USER_ID;
      this.$http.post("/comment", data).then(({ data: res }) => {
        this.shuruflage = false
        if (res.ok == 0)
          return this.$notify.error({
            title: "暂不支持表情输入",
            customClass: "alter"
          });
          this.getmsg()
      });
    }
  },
  created() {
    this.getmsg();
  },
  watch: {
    allnavs: {
      handler(allnavs) {
        // 判断是否允许访问
        if (allnavs.navs && allnavs.navs.split(",")[5] != 1) {
          return this.$router.push("/");
        }
      },
      // 深度监听
      deep: true
    }
  }
};
</script>

<style>
.leavemessage .deletebtn{
  position: absolute;
  right: 0;
  top: 0;
  z-index: 9;
}
.leavemessage .deletebtn span{
  color: black;
}
p.pright {
  text-align: right;
  text-decoration: underline;
  cursor: pointer;
  color: aqua;
}
.messagebox {
  /* padding: 25px; */
  /* border: 1px solid red; */
  max-width: 1000px;
  margin: 0 auto;
  padding-bottom: 60px;
}
.messagebox::after  {
  content: '已到最底部咯~';
  display: block;
  text-align: center;
  font-size: 12px;
  color: gray;
  opacity: 0.7;
}
.messagebox .messagebox {
  /* max-width: 90%; */
  margin: 0;
}
.messagebox > .msgitem {
  margin: 10px;
}
.msgitem > .head > .el-avatar {
  width: 30px;
  height: 30px;
  float: left;
  margin-right: 10px;
}
.head > .name {
  color: #9b8878;
}
.head > .time {
  color: #808080;
  font-size: 12px;
}
.msgitem > .msg {
  padding-left: 40px;
}
.msgitem{
  position: relative;
}

.pinlunbox {
  position: sticky;
  bottom: 0%;
  max-width: 1000px;
  height: 161px;
  width: 100%;
  margin: 0 auto;
  transition: height 0.6s;
  padding-left: 60px;
  background-color: #161618;
  border-radius: 15px;
  z-index: 12;
}
.pinlunbox.h {
  height: 40px;
  background-color: transparent;
}
</style>