<template>
  <div class="myeditor">
    <div class="shurukuang">
      <textarea v-model="editorContent"></textarea>
    </div>
    <div class="queding cursorp" @click="fasong">发送</div>
    <div>
      <img :class="{'head_select':selectIndex == i}" v-for="(item,i) in head_img_arr" :key="i" :src="item" alt @click="selectIndex = i" />
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      editorContent: "",
      editor: "",
      head_img_arr:[
        require('../assets/img/aa.jpeg'),
        require('../assets/img/bb.jpeg'),
        require('../assets/img/cc.jpeg'),
        require('../assets/img/dd.jpg'),
        require('../assets/img/ee.jpeg'),
        require('../assets/img/ff.jpeg'),
      ],
      names:[
        '小蓝',
        '小黄',
        '加油武汉',
        '小眼睛',
        '哦买噶',
        '小人甲',
      ],
      selectIndex:0
    };
  },
  methods: {
    fasong() {
      this.$emit('change',{
        comment:this.editorContent.replace(/\n/g,'<br />'),
        head_img:this.head_img_arr[this.selectIndex],
        name:this.names[this.selectIndex],
      })
      // 清空内容
      this.editorContent = ''
    }
  }
};
</script>

<style>
.myeditor .queding {
  width: 20%;
  display: inline-block;
  font-size: 14px;
  color: cadetblue;
  border: 1px solid;
  text-align: center;
  border-radius: 3px;
  padding: 10px;
  vertical-align: middle;
  max-width: 80px;
}
.myeditor .queding:hover {
  color: black;
  background-color: gray;
}
.shurukuang {
  display: inline-block;
  vertical-align: middle;
  width: 74%;
  margin-right: 2%;
  margin-left: 1%;
  border-radius: 15px;
  padding: 10px;
  box-sizing: border-box;
  background-color: white;
}
.shurukuang textarea {
  width: 100%;
  height: 100%;
  border: none;
  outline: none;
  font-size: 14px;
}
.shurukuang textarea::-webkit-scrollbar {
  display: none;
}
.myeditor img {
  width: 40px;
  height: 40px;
  vertical-align: middle;
  border-radius: 50%;
  border: 1px solid transparent;
  margin: 3px;
}
.myeditor img:hover,.myeditor img.head_select {
  border-color: violet;
  box-shadow: 0 0 8px 1px violet;
}
</style>